@charset "UTF-8";
/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.tablopost {
  color: white; }

.tablopost::after {
  content: "";
  position: absolute;
  height: 80%;
  width: 100%;
  top: 20%;
  left: 0;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.8)));
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0.8) 100%);
  opacity: 1;
  -webkit-transition: all 0.3s ease;
  transition: all 0.5s ease; }

.tablopost:hover::after {
  opacity: 0; }

.tablopost .featpost_a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.tablopost amp-img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.tablopost amp-img > img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s ease; }

.tablopost:hover amp-img > img {
  transform: scaleZ(1) scale(1.05); }

.tablopost .caption {
  display: block;
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  right: 1rem;
  height: fit-content;
  z-index: 2;
  font-size: 12px;
  letter-spacing: 0.1rem;
  transition: all 0.5s ease; }

.tablopost:hover .caption {
  opacity: 0.2; }

.tablopost .caption h3 {
  font-weight: 900;
  font-size: 50px;
  line-height: 1.25;
  letter-spacing: 1px;
  margin: 0.5rem 0;
  text-shadow: 2px 2px 7px rgba(0, 0, 0, 0.5); }

.tablopost .caption .subline {
  font-weight: 900;
  font-size: 12px;
  letter-spacing: 0.1rem;
  opacity: 0.75;
  text-shadow: 4px 4px 7px rgba(0, 0, 0, 0.25); }

.tablopost .caption .subline .time {
  font-weight: 400;
  opacity: 0.75; }

.tablopost .caption .tag {
  display: block;
  width: fit-content;
  padding: 0.25rem;
  background: #3ace00;
  color: white;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25); }

.twitterhero {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-decoration: none;
  color: white;
  overflow: hidden; }

.twitterhero img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s ease; }

.twitterhero::after {
  content: "";
  background: rgba(255, 255, 255, 0.33);
  position: absolute;
  width: 100%;
  height: 100%; }

.twitterhero .twcontent {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 1rem;
  z-index: 2; }

.twitterhero .twitter-container {
  width: 66%;
  backdrop-filter: blur(7px); }

@media (min-width: 768px) and (max-width: 1018px) {
  .twitterhero .twitter-container {
    width: 80%;
    backdrop-filter: blur(7px); } }

@media (max-width: 767px) {
  .twitterhero .twitter-container {
    width: 100%; } }

